@import "variables";

h1, h2, h3, h4, h5, h6 {
  color: $text-color-1;
}

body {
  background: $background-primary;
  min-height: 100vh;
}

.navbar-brand:active {
  filter: brightness(90%);
  transform: scale(98%);
}

@each $i in $max-width-rem-values {
  .mw-rem-#{$i}{
    max-width: #{$i}rem;
  }
}
